.divDetail{
	margin: 5px;
	padding: 5px;
	display: flex;
	flex-direction: column;
    justify-content: center;
    align-items: center;
}

.detalleBtn{
	background-color: #8FC1D4;
	font-size: large;
	text-align: center;
	margin: 10px;
	height: 30px;
	width: 150px;
    border-radius: 8px;
    border: 1px solid #345764;
	box-shadow: 1px 1px 3px #3F1F0E;
	cursor: pointer;
}

.detalleBtn:hover{
	filter: brightness(110%);
	box-shadow: inset 1px 1px 3px 0px #345764;
}

.detalle{
	font-weight: 600;
	font-size: larger;
	width: 40vw;
	border-bottom: 2px solid #865439;
	padding: 5px 0 2px 0;
	margin: 10px;
}

.btns{
	margin: 10px;
	display: flex;
	flex-direction: row;
    justify-content: center;
    align-items: center;
}

.divText{
	margin: 10px;
	display: flex;
	flex-direction: column;
    justify-content: center;
    align-items: center;
}

.textos{
	border: 0px;
	width: 60vw;
	font-size: large;
	text-align: center;
}